.sidebarRow{
    display: flex;
    align-items: center;
    padding: 10px 20px;
}
.sidebarRow_Icons{
  color: gray;  
}
.sidebarRow_Title{
    flex: 1;
    margin-left: 20px;
    font-size: 12px;
    font-weight: 500;

}
.sidebarRow:hover{
    background-color: lightgray;
    cursor: pointer;
}
.sidebarRow:hover > .sidebarRow_Icons{
   color: red;
}
.sidebarRow:hover > .sidebarRow_Title{
   font-weight: bold;
}
.sidebarRow.selected{
    background-color:lightgray
;
}
.sidebarRow.selected > .sidebarRow_Icons{
    color: red;
 }
.sidebarRow.selected > .sidebarRow_Title{
    font-weight: bold;
 }