.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color: white;
    width: auto;
  box-sizing: border-box;
}
/* added parent div to flex */
.header_Left{
    display: flex;
    align-items: center;
    
}
.header_logo{
    height:25px;
    object-fit: contain;
    margin:0  20px;
    /* changed margin */
}
.header_Input{
    display: flex;
    align-items: center;
 /* /changed  percent to rem */
    width: 30rem;
    border: 1px solid black;
  
   
}
.header_Input > input{
    flex: 1;
    border: none;
    
   
}
.header_InputButton{
    width: 50px !important;
    background-color: #fafafa;
    border: 1px solid lightgray;
    color: gray;
    


   
    
}

.header_Icon{
    display: flex;
    align-items: center;
     /* margin-left: auto; */
}
.header_Icons{
    margin-right: 8px;
}
